import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import About from '@/components/About'
import Authorization from '@/components/Authorization'
import Case from '@/components/Case'
import ChampionCase from '@/components/ChampionCase'
import Contact from '@/components/Contact'
import Join from '@/components/Join'
import SecooCase from '@/components/SecooCase'
import Services from '@/components/Services'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home,
      meta: {
        title: '思空 | 首页'   // 标题设置
      }
    }, 
    {
      path: '/about',
      name: 'about',
      component: About,
      meta: {
        title: '思空 | 关于思空'   // 标题设置
      }
    },
    {
      path: '/authorization',
      name: 'authorization',
      component: Authorization,
      meta: {
        title: '思空 | IP授权'   // 标题设置
      }
    },
    {
      path: '/case',
      name: 'case',
      component: Case,
      meta: {
        title: '思空 | 案例介绍'   // 标题设置
      }
    },
    {
      path: '/championCase',
      name: 'championCase',
      component: ChampionCase,
      meta: {
        title: '思空 | 拳王争霸赛案例'   // 标题设置
      }
    },
    {
      path: '/contact',
      name: 'contact',
      component: Contact,
      meta: {
        title: '思空 | 联系我们'   // 标题设置
      }
    },
    {
      path: '/join',
      name: 'join',
      component: Join,
      meta: {
        title: '思空 | 加入我们'   // 标题设置
      }
    },
    {
      path: '/secooCase',
      name: 'secooCase',
      component: SecooCase,
      meta: {
        title: '思空 | 寺库双十一系列营销案例'   // 标题设置
      }
    },
    {
      path: '/services',
      name: 'services',
      component: Services,
      meta: {
        title: '思空 | 服务介绍'   // 标题设置
      }
    },
  ]
})



